import React from 'react';
import PropTypes from 'prop-types';
import { Box, Typography } from '@mui/material';

const HowToPlay = ({ maxBet, houseCutPercentage }) => {
    const textColor = '#000000';
    const backgroundColor = 'rgba(255, 255, 255, 0.7)';
    const borderColor = '#2d5a3f';

    return (
        <Box sx={{
            p: 3,
            background: backgroundColor,
            borderRadius: 1,
            border: `1px solid ${borderColor}`,
            height: '100%'
        }}>
            <Typography variant="h5" gutterBottom sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1.5rem',
                mb: 2
            }}>
                How to Play
            </Typography>

            <Typography variant="h6" sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1.2rem',
                mb: 1
            }}>
                Game Rules
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mb: 2
            }}>
                • Place your bet on either Banker or Player
            </Typography>

            <Typography variant="h6" sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1.2rem',
                mb: 1
            }}>
                Minimum Bets:
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mb: 0.5
            }}>
                • Player: 1 $BOB
            </Typography>
            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mb: 2
            }}>
                • Banker: 2 $BOB (1 token goes to house)
            </Typography>

            <Typography variant="h6" sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1.2rem',
                mb: 1
            }}>
                Game Process:
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mb: 2
            }}>
                The game uses a 2-block system for fairness:
                <br />• First block: Your bet is committed
                <br />• Second block: Game is resolved using block hash
            </Typography>

            <Typography variant="h6" sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1.2rem',
                mb: 1
            }}>
                Payouts:
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mb: 0.5
            }}>
                • Banker: 1:1 (minus {houseCutPercentage}% house cut)
                <br />• Player: 1:1 (no house cut)
                <br />• Tie: Push (bets returned)
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mt: 2
            }}>
                Maximum bet: {maxBet} $BOB
            </Typography>

            <Typography sx={{
                color: textColor,
                fontWeight: 'bold',
                fontSize: '1rem',
                mt: 1
            }}>
                House cut ({houseCutPercentage}%) applies only to Banker bets
            </Typography>
        </Box>
    );
};

HowToPlay.propTypes = {
    maxBet: PropTypes.string.isRequired,
    houseCutPercentage: PropTypes.string.isRequired
};

export default HowToPlay; 