import React from 'react';
import PropTypes from 'prop-types';
import { Box } from '@mui/material';

const CasinoFelt = ({ children }) => {
    return (
        <Box sx={{
            position: 'relative',
            width: '100%',
            height: '100%',
            overflow: 'hidden',
            backgroundColor: '#1a472a',  // Dark green background
            '&::before': {
                content: '""',
                position: 'absolute',
                top: 0,
                left: 0,
                right: 0,
                bottom: 0,
                background: 'radial-gradient(circle at center, #2d5a3f 0%, #1a472a 100%)',
                opacity: 0.5,
                pointerEvents: 'none'
            }
        }}>
            {children}
        </Box>
    );
};

CasinoFelt.propTypes = {
    children: PropTypes.node.isRequired
};

export default CasinoFelt; 