import React, { createContext, useState, useContext } from 'react';
import PropTypes from 'prop-types';
import { ThemeProvider, createTheme } from '@mui/material';

const ThemeContext = createContext();

export const useThemeToggle = () => {
    return useContext(ThemeContext);
};

export const ThemeProviderWrapper = ({ children }) => {
    const [isDarkMode, setIsDarkMode] = useState(true);

    const theme = createTheme({
        palette: {
            mode: isDarkMode ? 'dark' : 'light',
            primary: {
                main: isDarkMode ? '#6B46C1' : '#2d5a3f',
                light: isDarkMode ? '#805AD5' : '#1a472a',
                dark: isDarkMode ? '#553C9A' : '#1a472a',
            },
            background: {
                default: isDarkMode ? '#000000' : '#ffffff',
                paper: isDarkMode ? '#2D2D2D' : '#ffffff',
            },
            text: {
                primary: isDarkMode ? '#FFFFFF' : '#000000',
                secondary: isDarkMode ? '#6B46C1' : '#2d5a3f',
            },
        },
        typography: {
            fontFamily: '"Playfair Display", "Roboto", "Helvetica", "Arial", sans-serif',
            h1: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 700,
            },
            h2: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 600,
            },
            h3: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 600,
            },
            h4: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 600,
            },
            h5: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 600,
            },
            h6: {
                fontFamily: '"Playfair Display", serif',
                fontWeight: 600,
            },
        },
        components: {
            MuiButton: {
                styleOverrides: {
                    root: {
                        borderRadius: 8,
                        textTransform: 'none',
                        fontWeight: 600,
                    },
                    contained: {
                        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
                        '&:hover': {
                            boxShadow: '0 6px 8px rgba(0, 0, 0, 0.2)',
                        },
                    },
                },
            },
            MuiPaper: {
                styleOverrides: {
                    root: {
                        backgroundImage: 'none',
                        boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)',
                    },
                },
            },
        },
    });

    const toggleTheme = () => {
        setIsDarkMode(prev => !prev);
    };

    return (
        <ThemeContext.Provider value={{ isDarkMode, toggleTheme }}>
            <ThemeProvider theme={theme}>
                {children}
            </ThemeProvider>
        </ThemeContext.Provider>
    );
};

ThemeProviderWrapper.propTypes = {
    children: PropTypes.node.isRequired,
};

export default ThemeProviderWrapper; 