import React, { useState, useEffect, useCallback } from 'react';
import { ethers } from 'ethers';
import {
    Box,
    Button,
    Container,
    Typography,
    Grid,
    Table,
    TableBody,
    TableCell,
    TableContainer,
    TableHead,
    TableRow,
    TextField,
} from '@mui/material';
import CasinoFelt from './components/CasinoFelt';
import HowToPlay from './components/HowToPlay';
import { CONTRACT_ADDRESS, BOB_TOKEN_ADDRESS, CONTRACT_ABI, BOB_ABI } from './config';
import { useThemeToggle } from './context/ThemeContext';

function App() {
    const { isDarkMode } = useThemeToggle();
    const textColor = '#000000';
    const borderColor = '#2d5a3f';
    const buttonGradient = 'linear-gradient(45deg, #1a472a 30%, #2d5a3f 90%)';
    const buttonHoverGradient = 'linear-gradient(45deg, #2d5a3f 30%, #1a472a 90%)';

    const [account, setAccount] = useState(null);
    const [contract, setContract] = useState(null);
    const [bobToken, setBobToken] = useState(null);
    const [provider, setProvider] = useState(null);
    const [walletBalance, setWalletBalance] = useState('0');
    const [contractBalance, setContractBalance] = useState('0');
    const [houseBalance, setHouseBalance] = useState('0');
    const [betAmount, setBetAmount] = useState('');
    const [gameStatus, setGameStatus] = useState(null);
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState('');
    const [currentGame, setCurrentGame] = useState(null);
    const [canResolve, setCanResolve] = useState(false);
    const [gameHistory, setGameHistory] = useState([]);
    const [countdown, setCountdown] = useState(null);
    const [autoResolve, setAutoResolve] = useState(false);
    const [maxBet, setMaxBet] = useState('10');
    const [houseCutPercentage, setHouseCutPercentage] = useState('5');
    const [resolveCountdown, setResolveCountdown] = useState(30);
    const [isResolvePending, setIsResolvePending] = useState(false);

    const updateBalances = useCallback(async () => {
        if (account && contract && bobToken) {
            try {
                const walletBal = await bobToken.balanceOf(account);
                const contractBal = await contract.balances(account, BOB_TOKEN_ADDRESS);
                const houseBalance = await contract.houseBalances(BOB_TOKEN_ADDRESS);
                setHouseBalance(houseBalance.toString());
                setWalletBalance(walletBal.toString());
                setContractBalance(contractBal.toString());
            } catch (err) {
                console.error('Error updating balances:', err);
            }
        }
    }, [account, contract, bobToken]);

    const checkGameStatus = useCallback(async () => {
        if (account && contract && provider) {
            try {
                const game = await contract.games(account);

                if (game && game.commitBlock && game.commitBlock !== BigInt(0)) {
                    setCurrentGame(game);
                    const currentBlock = await provider.getBlockNumber();

                    const blockDiff = currentBlock - Number(game.commitBlock);

                    if (blockDiff > 255) {
                        setGameStatus('Game expired - too many blocks have passed. Please start a new game.');
                        setCanResolve(false);
                        setCountdown(null);
                    } else if (blockDiff <= 0) {
                        setGameStatus('Waiting for next block...');
                        setCanResolve(false);
                        setCountdown(1);
                    } else {
                        setCanResolve(true);
                        setCountdown(0);
                    }
                } else {
                    setCurrentGame(null);
                    setCanResolve(false);
                    setGameStatus(null);
                    setCountdown(null);
                }
            } catch (err) {
                console.error('Error checking game status:', err);
                setCurrentGame(null);
                setCanResolve(false);
                setGameStatus(null);
                setCountdown(null);
            }
        }
    }, [account, contract, provider]);

    const loadGameHistory = useCallback(async () => {
        if (!account || !contract || !provider) return;

        try {
            const currentBlock = await provider.getBlockNumber();
            const fromBlock = currentBlock - 10000;

            const startFilter = contract.filters.GameStarted(account);
            const resolveFilter = contract.filters.GameResolved(account);

            const startEvents = await contract.queryFilter(startFilter, fromBlock);
            const resolveEvents = await contract.queryFilter(resolveFilter, fromBlock);

            const history = [];
            for (const startEvent of startEvents) {
                const args = startEvent.args;

                const resolveEvent = resolveEvents.find(e => {
                    return e.args[0] === args[0] && e.blockNumber > startEvent.blockNumber;
                });

                const block = await provider.getBlock(startEvent.blockNumber);
                const timestamp = new Date(block.timestamp * 1000);

                const gameData = {
                    timestamp,
                    amount: args[2].toString(),
                    position: args[3] === 0n ? 'Banker' : 'Player',
                    result: resolveEvent ?
                        (resolveEvent.args[2] === 0n ? 'Banker Won' :
                            resolveEvent.args[2] === 1n ? 'Player Won' : 'Tie') :
                        'Pending',
                    payout: resolveEvent ? resolveEvent.args[3].toString() : '0',
                    txHash: resolveEvent ? resolveEvent.transactionHash : startEvent.transactionHash,
                    player: args[0],
                    isResolved: !!resolveEvent
                };

                history.push(gameData);
            }

            history.sort((a, b) => b.timestamp - a.timestamp);
            setGameHistory(history);

            const lastGame = history[0];
            if (lastGame && lastGame.result === 'Pending') {
                const currentBlock = await provider.getBlockNumber();
                const gameStartBlock = (await provider.getTransaction(lastGame.txHash)).blockNumber;

                if (currentBlock > gameStartBlock + 1) {
                    const game = await contract.games(account);
                    if (game && game.commitBlock && game.commitBlock !== BigInt(0)) {
                        setCurrentGame(game);
                        setCanResolve(true);
                        setGameStatus('Game can be resolved now!');
                    }
                }
            }

            await updateBalances();
        } catch (err) {
            console.error('Error loading game history:', err);
        }
    }, [account, contract, provider, updateBalances]);

    useEffect(() => {
        if (account && contract && provider) {
            loadGameHistory();
            const interval = setInterval(loadGameHistory, 30000);
            return () => clearInterval(interval);
        }
    }, [account, contract, provider, loadGameHistory]);

    const connectWallet = async () => {
        try {
            if (window.ethereum) {
                const provider = new ethers.BrowserProvider(window.ethereum);
                const accounts = await provider.send("eth_requestAccounts", []);
                const signer = await provider.getSigner();

                setAccount(accounts[0]);
                setProvider(provider);

                const contract = new ethers.Contract(CONTRACT_ADDRESS, CONTRACT_ABI, signer);
                const bobContract = new ethers.Contract(BOB_TOKEN_ADDRESS, BOB_ABI, signer);

                setContract(contract);
                setBobToken(bobContract);

                // Fetch contract parameters
                const [currentMaxBet, currentHouseCut] = await Promise.all([
                    contract.maxBet(),
                    contract.houseCutPercentage()
                ]);
                setMaxBet(currentMaxBet.toString());
                setHouseCutPercentage(currentHouseCut.toString());

                try {
                    const isAllowed = await contract.allowedTokens(BOB_TOKEN_ADDRESS);
                    if (!isAllowed) {
                        setError('This token is not allowed in the contract');
                        return;
                    }
                } catch (tokenErr) {
                    console.error('Error checking token allowance:', tokenErr);
                }

                await updateBalances();
                await checkGameStatus();
                await loadGameHistory();
            } else {
                setError('Please install MetaMask to use this application');
            }
        } catch (err) {
            console.error('Error connecting wallet:', err);
            setError('Failed to connect wallet');
        }
    };

    const isValidAmount = (amount, position) => {
        if (!amount || amount === '') {
            setError('Please enter an amount');
            return false;
        }
        try {
            const bigIntAmount = BigInt(amount);
            if (bigIntAmount <= BigInt(0)) {
                setError('Amount must be greater than 0');
                return false;
            }

            // For deposits, check against half of house balance
            if (position === null) {
                const halfHouseBalance = BigInt(houseBalance) / BigInt(2);
                if (bigIntAmount > halfHouseBalance) {
                    setError(`Deposit cannot exceed ${halfHouseBalance.toString()} $BOB (half of house balance)`);
                    return false;
                }
                setError(''); // Clear error if validation passes
                return true;
            }

            // For bets, check max bet and minimum requirements
            if (bigIntAmount > BigInt(maxBet)) {
                setError(`Bet amount cannot exceed ${maxBet} $BOB`);
                return false;
            }

            // Banker position (0) requires minimum 2 tokens
            if (position === 0 && bigIntAmount <= BigInt(1)) {
                setError('Banker bets must be at least 2 $BOB (1 token goes to house)');
                return false;
            }

            setError(''); // Clear error if validation passes
            return true;
        } catch {
            setError('Please enter a valid number');
            return false;
        }
    };

    const deposit = async () => {
        try {
            setLoading(true);
            setError('');

            if (!isValidAmount(betAmount, null)) {
                return;
            }

            const amount = BigInt(betAmount);

            const allowance = await bobToken.allowance(account, CONTRACT_ADDRESS);

            if (allowance < amount) {
                try {
                    const approveTx = await bobToken.approve(CONTRACT_ADDRESS, amount);
                    await approveTx.wait();
                } catch (approveErr) {
                    console.error('Error during token approval:', approveErr);
                    throw new Error('Failed to approve tokens. Please try again.');
                }
            }

            try {
                const depositTx = await contract.deposit(amount, BOB_TOKEN_ADDRESS);
                await depositTx.wait();
            } catch (depositErr) {
                console.error('Error during deposit:', depositErr);
                if (depositErr.message.includes('insufficient allowance')) {
                    throw new Error('Insufficient token allowance. Please try approving again.');
                } else if (depositErr.message.includes('token not allowed')) {
                    throw new Error('This token is not allowed in the contract.');
                } else {
                    throw depositErr;
                }
            }

            await updateBalances();
            setBetAmount('');
            setGameStatus('Deposit successful!');
        } catch (err) {
            console.error('Deposit error:', err);
            console.error('Error details:', {
                code: err.code,
                message: err.message,
                data: err.data,
                transaction: err.transaction
            });
            setError(err.message || 'Error during deposit');
        } finally {
            setLoading(false);
        }
    };

    const startGame = async (position) => {
        try {
            setLoading(true);
            setError('');
            setGameStatus('Validating bet amount...');

            if (!isValidAmount(betAmount, position)) {
                if (!error) setError('Please enter a valid amount');
                return;
            }

            const amount = BigInt(betAmount);

            if (BigInt(contractBalance) < amount) {
                setError('Insufficient balance in contract. Please deposit first.');
                return;
            }

            if (currentGame && currentGame.commitBlock !== 0) {
                setError('You have an active game. Please resolve it first.');
                return;
            }

            setGameStatus('Initiating game transaction...');
            const tx = await contract.startGame(amount, position, BOB_TOKEN_ADDRESS);
            setGameStatus('Bet submitted. Waiting for confirmation...');
            setBetAmount('');
            setAutoResolve(true);

            await tx.wait();
            setGameStatus('Game started! Waiting for block confirmations (this may take 30-60 seconds)...');
            await checkGameStatus();

            // Reduce polling frequency to prevent flickering
            const interval = setInterval(async () => {
                const currentBlock = await provider.getBlockNumber();
                const gameBlock = Number(currentGame?.commitBlock || 0);
                const blockDiff = currentBlock - gameBlock;

                if (blockDiff >= 2) {
                    clearInterval(interval);
                    setGameStatus('Awaiting Resolve, please confirm when ready');
                    if (autoResolve) {
                        resolveGame();
                    }
                } else if (blockDiff === 1) {
                    setGameStatus('Waiting for one more block confirmation...');
                } else {
                    setGameStatus('Waiting for block confirmations...');
                }
            }, 5000); // Check every 5 seconds instead of 2

            setTimeout(() => clearInterval(interval), 300000);
        } catch (err) {
            setError(err.message);
            setGameStatus(null);
        } finally {
            setLoading(false);
        }
    };

    const resolveGame = useCallback(async () => {
        try {
            setLoading(true);
            setError('');
            setGameStatus('Preparing to resolve game...');

            if (!currentGame || !currentGame.commitBlock) {
                setError('No valid game to resolve');
                return;
            }

            const currentBlock = await provider.getBlockNumber();
            const blockDiff = currentBlock - Number(currentGame.commitBlock);

            if (blockDiff <= 0) {
                setError('Cannot resolve game yet. Please wait for the next block.');
                return;
            }

            if (blockDiff > 255) {
                setError('Game has expired. Please start a new game.');
                return;
            }

            if (blockDiff < 2) {
                setGameStatus('Waiting for block confirmations...');
                return;
            }

            setGameStatus('Initiating resolution transaction...');
            const tx = await contract.resolveGame();
            setGameStatus('Resolution transaction submitted. Waiting for confirmation...');

            const receipt = await tx.wait();

            const resolveEvent = receipt.logs
                .map(log => {
                    try {
                        return contract.interface.parseLog(log);
                    } catch (e) {
                        return null;
                    }
                })
                .find(event => event && event.name === 'GameResolved');

            if (resolveEvent) {
                const result = resolveEvent.args[2] === 0n ? 'Banker Won' :
                    resolveEvent.args[2] === 1n ? 'Player Won' : 'Tie';
                const payout = resolveEvent.args[3].toString();
                const txHash = receipt.hash;
                const explorerUrl = `https://etc.blockscout.com/tx/${txHash}`;
                setGameStatus(
                    `Game Complete! Result: ${result}, Payout: ${payout} $BOB\n` +
                    `Player: ${account}\n` +
                    `Verify on chain: ${explorerUrl}`
                );
            }

            await Promise.all([
                updateBalances(),
                checkGameStatus(),
                loadGameHistory(),
                account
            ]);

            setCanResolve(false);
            setAutoResolve(false);
        } catch (err) {
            console.error('Error resolving game:', err);
            if (err.message.includes('Blockhash unavailable')) {
                setError('Game has expired or block hash is no longer available. Please start a new game.');
                setCurrentGame(null);
                setCanResolve(false);
                setGameStatus(null);
                setCountdown(null);
            } else {
                setError(err.message);
            }
        } finally {
            setLoading(false);
        }
    }, [contract, currentGame, provider, updateBalances, checkGameStatus, loadGameHistory, account]);

    useEffect(() => {
        if (autoResolve && canResolve && !loading) {
            resolveGame();
        }
    }, [autoResolve, canResolve, loading, resolveGame]);

    useEffect(() => {
        let interval;
        if (countdown !== null && countdown > 0) {
            interval = setInterval(() => {
                setCountdown(prev => {
                    if (prev <= 1) {
                        checkGameStatus();
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);
        }
        return () => clearInterval(interval);
    }, [countdown, checkGameStatus]);

    useEffect(() => {
        let interval;
        if (canResolve && !isResolvePending) {
            setIsResolvePending(true);
            setResolveCountdown(30);
            interval = setInterval(() => {
                setResolveCountdown(prev => {
                    if (prev <= 1) {
                        clearInterval(interval);
                        return 0;
                    }
                    return prev - 1;
                });
            }, 1000);
        }
        return () => {
            clearInterval(interval);
        };
    }, [canResolve, isResolvePending]);

    // Reset resolve countdown when game changes
    useEffect(() => {
        if (!currentGame) {
            setIsResolvePending(false);
            setResolveCountdown(30);
        }
    }, [currentGame]);

    const withdraw = async () => {
        try {
            setLoading(true);
            if (currentGame && currentGame.commitBlock !== 0) {
                setError('Cannot withdraw while game is in progress');
                return;
            }
            await contract.withdraw(BigInt(contractBalance), BOB_TOKEN_ADDRESS);
            await updateBalances();
        } catch (err) {
            setError(err.message);
        } finally {
            setLoading(false);
        }
    };

    const buttonStyle = {
        background: buttonGradient,
        '&:hover': {
            background: buttonHoverGradient,
        },
        color: '#ffffff',  // White text for better contrast
        '&.Mui-disabled': {
            color: 'rgba(255, 255, 255, 0.5)'  // Semi-transparent white when disabled
        }
    };

    const outlinedButtonStyle = {
        borderColor: borderColor,
        color: textColor,
        '&:hover': {
            borderColor: '#1a472a',
            color: '#2d5a3f',
        },
        position: 'relative',
        zIndex: 2,
        '&.Mui-disabled': {
            color: 'rgba(0, 0, 0, 0.3)'  // Semi-transparent black when disabled
        }
    };

    return (
        <Container maxWidth="lg" sx={{
            p: 0,
            height: '100vh',
            position: 'relative',
            zIndex: 1
        }}>
            <CasinoFelt>
                <Box sx={{
                    width: '100%',
                    height: '100vh',
                    display: 'flex',
                    flexDirection: 'column',
                    gap: 2,
                    position: 'relative',
                    zIndex: 2,
                    '&::before': {
                        content: '""',
                        position: 'absolute',
                        top: '50%',
                        left: '50%',
                        transform: 'translate(-50%, -50%)',
                        width: '500px',
                        height: '500px',
                        background: `url(https://etc.blockscout.com/assets/configs/network_icon_dark.svg) no-repeat center center`,
                        backgroundSize: 'contain',
                        opacity: 0.05,
                        zIndex: -1,
                        pointerEvents: 'none'
                    }
                }}>
                    {/* Top Bar with Title, Connect Button, and Status */}
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center',
                        justifyContent: 'space-between',
                        p: 2,
                        background: 'rgba(255, 255, 255, 0.7)',
                        borderRadius: 1
                    }}>
                        <Typography variant="h5" sx={{
                            color: textColor,
                            fontFamily: '"Playfair Display", serif',
                            fontWeight: 'bold',
                            fontSize: '1.5rem',
                            display: 'flex',
                            alignItems: 'center',
                            gap: 2
                        }}>
                            <img
                                src="https://etc.blockscout.com/assets/configs/network_icon_dark.svg"
                                alt="ETC"
                                style={{ width: '24px', height: '24px' }}
                            />
                            Baccarat ETC
                            <img
                                src="https://etc.blockscout.com/assets/configs/network_icon_dark.svg"
                                alt="ETC"
                                style={{ width: '24px', height: '24px' }}
                            />
                        </Typography>

                        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
                            {!account ? (
                                <Button
                                    variant="contained"
                                    onClick={connectWallet}
                                    disabled={loading}
                                    size="small"
                                    sx={buttonStyle}
                                >
                                    Connect Wallet
                                </Button>
                            ) : (
                                <Typography variant="body2" sx={{
                                    color: textColor,
                                    fontFamily: 'monospace',
                                    fontWeight: 'bold',
                                    fontSize: '1rem'
                                }}>
                                    {`${account.slice(0, 6)}...${account.slice(-4)}`}
                                </Typography>
                            )}

                            <Typography sx={{
                                color: error ? '#ff6b6b' : textColor,
                                fontSize: '1.1rem',
                                fontWeight: 'bold'
                            }}>
                                {error || gameStatus || 'Ready to play'}
                                {countdown > 0 && ` (${countdown})`}
                            </Typography>
                        </Box>
                    </Box>

                    {/* Scrollable Content Area */}
                    <Box sx={{
                        flex: 1,
                        overflow: 'auto',
                        px: 2,
                        pb: 2
                    }}>
                        <Grid container spacing={2}>
                            {/* Game Controls */}
                            <Grid item xs={12} md={8}>
                                {account && (
                                    <Box sx={{
                                        p: 3,
                                        background: 'rgba(255, 255, 255, 0.7)',
                                        borderRadius: 1
                                    }}>
                                        {/* Balances */}
                                        <Grid container spacing={2} sx={{ mb: 3 }}>
                                            <Grid item xs={4}>
                                                <Typography variant="body2" sx={{ color: textColor }}>
                                                    Wallet: {walletBalance} $BOB
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={4}>
                                                <Typography variant="body2" sx={{ color: textColor }}>
                                                    Deposited: {contractBalance} $BOB
                                                </Typography>
                                            </Grid>
                                            <Grid item xs={4}>
                                                <Typography variant="body2" sx={{ color: textColor }}>
                                                    House: {houseBalance} $BOB
                                                </Typography>
                                            </Grid>
                                        </Grid>

                                        {/* Game Controls */}
                                        <Box sx={{ mb: 3 }}>
                                            <Grid container spacing={2}>
                                                <Grid item xs={8}>
                                                    <TextField
                                                        fullWidth
                                                        label="Amount"
                                                        type="number"
                                                        value={betAmount}
                                                        onChange={(e) => setBetAmount(e.target.value)}
                                                        size="small"
                                                        sx={{
                                                            '& .MuiOutlinedInput-root': {
                                                                '& fieldset': {
                                                                    borderColor: borderColor,
                                                                },
                                                                '&:hover fieldset': {
                                                                    borderColor: '#805AD5',
                                                                },
                                                            },
                                                            '& .MuiInputLabel-root': {
                                                                color: textColor,
                                                            },
                                                            '& .MuiInputBase-input': {
                                                                color: textColor,
                                                            }
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item xs={4}>
                                                    <Button
                                                        fullWidth
                                                        variant="contained"
                                                        onClick={deposit}
                                                        disabled={loading || !betAmount}
                                                        size="small"
                                                        sx={buttonStyle}
                                                    >
                                                        Deposit
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                        </Box>

                                        {currentGame ? (
                                            <Box sx={{ position: 'relative', zIndex: 2 }}>
                                                <Typography variant="h6" gutterBottom sx={{ color: textColor }}>
                                                    Current Game: {currentGame.amount.toString()} $BOB on {currentGame.position === 0 ? 'Banker' : 'Player'}
                                                </Typography>
                                                {gameStatus && gameStatus.includes('Game Complete!') && (
                                                    <Box sx={{
                                                        mb: 2,
                                                        p: 2,
                                                        backgroundColor: 'rgba(255, 255, 255, 0.9)',
                                                        borderRadius: 1,
                                                        border: '1px solid #2d5a3f'
                                                    }}>
                                                        {gameStatus.split('\n').map((line, index) => (
                                                            <Typography
                                                                key={index}
                                                                sx={{
                                                                    color: textColor,
                                                                    mb: 1,
                                                                    '& a': {
                                                                        color: '#2d5a3f',
                                                                        textDecoration: 'none',
                                                                        '&:hover': {
                                                                            textDecoration: 'underline'
                                                                        }
                                                                    }
                                                                }}
                                                            >
                                                                {line.includes('Verify on chain:') ? (
                                                                    <>
                                                                        Verify on chain: <a href={line.split(': ')[1]} target="_blank" rel="noopener noreferrer">View Transaction</a>
                                                                    </>
                                                                ) : line}
                                                            </Typography>
                                                        ))}
                                                    </Box>
                                                )}
                                                {canResolve && (
                                                    <Button
                                                        variant="contained"
                                                        onClick={resolveGame}
                                                        disabled={loading || resolveCountdown > 0}
                                                        fullWidth
                                                        sx={{
                                                            ...buttonStyle,
                                                            position: 'relative',
                                                            overflow: 'hidden',
                                                            minHeight: '48px'
                                                        }}
                                                    >
                                                        <Box sx={{
                                                            position: 'relative',
                                                            zIndex: 2,
                                                            display: 'flex',
                                                            alignItems: 'center',
                                                            justifyContent: 'center',
                                                            gap: 1
                                                        }}>
                                                            {resolveCountdown > 0 ? (
                                                                <>
                                                                    <Typography variant="body1">
                                                                        Resolving in {resolveCountdown}s
                                                                    </Typography>
                                                                    <Box
                                                                        component="span"
                                                                        sx={{
                                                                            display: 'inline-block',
                                                                            width: '4px',
                                                                            height: '4px',
                                                                            borderRadius: '50%',
                                                                            backgroundColor: 'white',
                                                                            animation: 'pulse 1s infinite',
                                                                            '@keyframes pulse': {
                                                                                '0%': {
                                                                                    opacity: 1,
                                                                                    transform: 'scale(1)'
                                                                                },
                                                                                '50%': {
                                                                                    opacity: 0.5,
                                                                                    transform: 'scale(1.5)'
                                                                                },
                                                                                '100%': {
                                                                                    opacity: 1,
                                                                                    transform: 'scale(1)'
                                                                                }
                                                                            }
                                                                        }}
                                                                    />
                                                                </>
                                                            ) : (
                                                                'Resolve Game'
                                                            )}
                                                        </Box>
                                                        {resolveCountdown > 0 && (
                                                            <>
                                                                <Box
                                                                    sx={{
                                                                        position: 'absolute',
                                                                        bottom: 0,
                                                                        left: 0,
                                                                        height: '4px',
                                                                        backgroundColor: 'rgba(255, 255, 255, 0.3)',
                                                                        width: '100%'
                                                                    }}
                                                                />
                                                                <Box
                                                                    sx={{
                                                                        position: 'absolute',
                                                                        bottom: 0,
                                                                        left: 0,
                                                                        height: '4px',
                                                                        background: 'linear-gradient(90deg, rgba(255,255,255,0.8) 0%, rgba(255,255,255,0.4) 100%)',
                                                                        width: `${(resolveCountdown / 30) * 100}%`,
                                                                        transition: 'width 1s linear',
                                                                        boxShadow: '0 0 8px rgba(255,255,255,0.5)'
                                                                    }}
                                                                />
                                                            </>
                                                        )}
                                                    </Button>
                                                )}
                                            </Box>
                                        ) : (
                                            <Grid container spacing={2} sx={{ position: 'relative', zIndex: 2 }}>
                                                <Grid item xs={6}>
                                                    <Button
                                                        variant="contained"
                                                        onClick={() => startGame(0)}
                                                        disabled={loading || !betAmount}
                                                        fullWidth
                                                        sx={buttonStyle}
                                                    >
                                                        Bet Banker
                                                    </Button>
                                                </Grid>
                                                <Grid item xs={6}>
                                                    <Button
                                                        variant="contained"
                                                        onClick={() => startGame(1)}
                                                        disabled={loading || !betAmount}
                                                        fullWidth
                                                        sx={buttonStyle}
                                                    >
                                                        Bet Player
                                                    </Button>
                                                </Grid>
                                                <Grid item xs={12}>
                                                    <Button
                                                        variant="outlined"
                                                        onClick={withdraw}
                                                        disabled={loading || BigInt(contractBalance) === BigInt(0)}
                                                        fullWidth
                                                        sx={outlinedButtonStyle}
                                                    >
                                                        Withdraw Balance
                                                    </Button>
                                                </Grid>
                                            </Grid>
                                        )}
                                    </Box>
                                )}
                            </Grid>

                            {/* Rules Panel */}
                            <Grid item xs={12} md={4}>
                                <HowToPlay maxBet={maxBet} houseCutPercentage={houseCutPercentage} isDarkMode={isDarkMode} />
                            </Grid>

                            {/* Game History */}
                            {account && (
                                <Grid item xs={12}>
                                    <Box sx={{
                                        p: 3,
                                        background: 'rgba(255, 255, 255, 0.7)',
                                        borderRadius: 1,
                                        overflowX: 'auto'
                                    }}>
                                        <Typography variant="h6" gutterBottom sx={{
                                            color: textColor,
                                            fontWeight: 'bold',
                                            fontSize: '1.2rem'
                                        }}>
                                            Recent Games
                                        </Typography>
                                        <TableContainer sx={{ maxHeight: '300px' /* Set max height for vertical scroll */ }}>
                                            <Table size="small" stickyHeader /* Make header sticky */ >
                                                <TableHead>
                                                    <TableRow>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Time</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Amount</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Position</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Result</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Payout</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Player</TableCell>
                                                        <TableCell sx={{
                                                            color: textColor,
                                                            fontWeight: 'bold',
                                                            fontSize: '1rem',
                                                            backgroundColor: 'rgba(255, 255, 255, 0.9)'
                                                        }}>Transaction</TableCell>
                                                    </TableRow>
                                                </TableHead>
                                                <TableBody>
                                                    {gameHistory.map((game, index) => (
                                                        <TableRow key={index}>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '1rem' }}>{game.timestamp.toLocaleString()}</TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '1rem' }}>{game.amount} $BOB</TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '1rem' }}>{game.position}</TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '1rem' }}>{game.result}</TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '1rem' }}>{game.payout} $BOB</TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '0.85rem', fontFamily: 'monospace' }}>
                                                                {`${game.player.slice(0, 6)}...${game.player.slice(-4)}`}
                                                            </TableCell>
                                                            <TableCell sx={{ color: textColor, fontWeight: 'bold', fontSize: '0.85rem' }}>
                                                                <a
                                                                    href={`https://etc.blockscout.com/tx/${game.txHash}`}
                                                                    target="_blank"
                                                                    rel="noopener noreferrer"
                                                                    style={{
                                                                        color: '#2d5a3f',
                                                                        textDecoration: 'none',
                                                                        '&:hover': {
                                                                            textDecoration: 'underline'
                                                                        }
                                                                    }}
                                                                >
                                                                    {`${game.txHash.slice(0, 6)}...${game.txHash.slice(-4)}`}
                                                                </a>
                                                            </TableCell>
                                                        </TableRow>
                                                    ))}
                                                </TableBody>
                                            </Table>
                                        </TableContainer>
                                    </Box>
                                </Grid>
                            )}
                        </Grid>
                    </Box>
                </Box>
            </CasinoFelt>
        </Container>
    );
}

export default App; 